import * as types from './types';
var initLanSaga = function () {
    return {
        type: types.ACTION_TYPE.INIT_LAN_SAGA,
    };
};
var initLanAction = function (payload) {
    return {
        type: types.ACTION_TYPE.INIT_LAN,
        payload: payload,
    };
};
/**
 * 更新语言
 */
var updateEditLanAction = function (editLan) {
    return {
        type: types.ACTION_TYPE.UPDATE_EDIT_LAN,
        payload: {
            editLan: editLan,
        },
    };
};
/**
 * 更新默认语言
 */
var updateDefaultLanAction = function (defaultLan) {
    return {
        type: types.ACTION_TYPE.UPDATE_DEFAULT_LAN,
        payload: {
            defaultLan: defaultLan,
        },
    };
};
/**
 * 添加语言
 */
var addLanAction = function (lan) {
    return {
        type: types.ACTION_TYPE.ADD_LAN,
        payload: {
            lan: lan,
        },
    };
};
export { initLanSaga, initLanAction, updateEditLanAction, updateDefaultLanAction, addLanAction };
