import { combineReducers } from 'redux';
import nodesState from './nodesState/reducer';
import editState from './editState/reducer';
import lanState from './lanState/reducer';
import scriptState from './scriptState/reducer';
import reusableState from './reusableState/reducer';
import surveyInfoState from './surveyInfoState/reducer';
import appearanceState from "./appearanceState/reducer";
// combine all the reducer in here
var rootReducer = combineReducers({
    nodesState: nodesState,
    editState: editState,
    lanState: lanState,
    scriptState: scriptState,
    reusableState: reusableState,
    surveyInfoState: surveyInfoState,
    appearanceState: appearanceState
});
export default rootReducer;
